import React, { useState, useEffect } from 'react';

import Persons from './components/Persons';
import Filter from './components/Filter';
import PersonForm from './components/PersonForm';
import Notification from './components/Notification';

import personService from './services/persons';

const App = () => {
  const [persons, setPersons] = useState([]);
  const [filter, setFilter] = useState('');
  const [notification, setNotification] = useState({});

  useEffect(() => {
    personService.getAll().then(persons => setPersons(persons));
  }, []);

  const handleFilterChange = event => setFilter(event.target.value);

  const handleNotification = (status, message) => {
    setNotification({ status, message });
    setTimeout(() => {
      setNotification({});
    }, 1000);
  };

  const handleFormSubmit = (name, number) => {
    const person = persons.find(person => person.name === name);

    if (person) {
      if (
        window.confirm(
          `${name} is already added to the database. Replace the old number with the new one?`
        )
      ) {
        personService
          .updatePerson({ ...person, name, number })
          .then(newPerson => {
            setPersons(
              persons.map(el => (el.id === newPerson.id ? newPerson : el))
            );
            handleNotification('success', `Updated ${name}`);
          })
          .catch(() =>
            handleNotification(
              'error',
              `Information of ${name} has been already removed from the server`
            )
          );
      }
    } else {
      personService.createPerson({ name, number }).then(newPerson => {
        setPersons([...persons, newPerson]);
        handleNotification('success', `Added ${name}`);
      });
    }
  };

  const handleDelete = id => {
    const person = persons.find(person => person.id === id);

    if (window.confirm(`Delete ${person.name}`)) {
      personService
        .deletePerson(id)
        .then(res => {
          if (res === 'success') {
            setPersons(persons.filter(person => person.id !== id));
          }
        })
        .catch(() =>
          handleNotification(
            'error',
            `${person.name} does not exist on the server`
          )
        );
    }
  };

  const returnFiltered = () =>
    persons.filter(person =>
      person.name.toLowerCase().includes(filter.toLowerCase())
    );

  return (
    <div>
      <h2>Phonebook</h2>
      <Notification notification={notification} />
      <Filter filter={filter} handleChange={handleFilterChange} />

      <h2>add a new</h2>
      <PersonForm handleSubmit={handleFormSubmit} />

      <h2>Numbers</h2>
      <Persons persons={returnFiltered()} handleDelete={handleDelete} />
    </div>
  );
};

export default App;
