import React, { useState } from 'react';

const PersonForm = ({ handleSubmit }) => {
  const [newName, setNewName] = useState('');
  const [newNumber, setNewNumber] = useState('');
  // I chose to keep the name and number state on form instead of App.js as the controlled component's state should ideally stay where it's controlled
  const handleNameChange = event => setNewName(event.target.value);
  const handleNumberChange = event => setNewNumber(event.target.value);

  const handleFormSubmit = event => {
    event.preventDefault();
    setNewName('');
    setNewNumber('');
    handleSubmit(newName, newNumber);
  };

  return (
    <form onSubmit={handleFormSubmit}>
      <div>
        name: <input value={newName} onChange={handleNameChange} />
      </div>
      <div>
        number: <input value={newNumber} onChange={handleNumberChange} />
      </div>
      <div>
        <button type="submit">add</button>
      </div>
    </form>
  );
};

export default PersonForm;
