import React from 'react';
import Person from './Person';

const Persons = ({ persons, handleDelete }) => {
  return (
    <>
      {persons.map(({ name, number, id }) => (
        <Person
          key={name}
          name={name}
          number={number}
          handleDelete={() => handleDelete(id)}
        />
      ))}
    </>
  );
};

export default Persons;
